.penta-circle {
    $WIDTH: 30px;
    $PADDING: 5px;

    border: 1px solid white;
    border-radius: 50%;

    height: 10*$WIDTH;
    width: 10*$WIDTH;
    padding: $PADDING;
    margin: $PADDING;

    position: relative;

    .penta-note, .penta-note-minor {
        position: absolute;

        border: 1px solid white;
        border-radius: 50%;

        padding: $PADDING;

        text-align: center;

        display: inline-block;
    }
    .penta-note {
        top: 4.5*$WIDTH;
        left: 4.5*$WIDTH;
        width: $WIDTH;
        height: $WIDTH;
        line-height: calc($WIDTH - $PADDING);
    }
    .penta-note-minor {
        top: 4.7*$WIDTH;
        left: 4.7*$WIDTH;
        width: 0.6*$WIDTH;
        height: 0.6*$WIDTH;
        line-height: calc(0.6*$WIDTH - $PADDING);
        font-size: 0.8rem;
    }
    @for $i from 1 through 12 {
        .penta-note:nth-child(#{$i}), .penta-note:nth-child(#{$i+12}) {
            background: hsl($i * 25, 50%, 60%);
            color: hsl($i * 25, 70%, 30%);
        }
        .penta-note-major:nth-child(#{$i}) {
            transform:
                rotate(-120deg)
                rotate($i*30deg)
                translate(4*$WIDTH, 0*$WIDTH)
                rotate(120deg)
                rotate(-$i*30deg)
            ;
        }
        .penta-note-minor:nth-child(#{$i+12}) {
            transform:
                rotate(-210deg)
                rotate($i*30deg)
                translate(2.6*$WIDTH, 0*$WIDTH)
                rotate(210deg)
                rotate(-$i*30deg)
            ;
        }
    }
}